
import moment from 'moment'
import common from '@/libs/common.mixin'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default{
mixins: [common],
components: {
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
data_uri: 'orders',
data: [],
}
},
mounted () {

},
methods: {
getImg (row) {
let obj = row.order_items[0],
result = ''
if(obj.lesson!=null){
result = obj.lesson.lesson_cover_picture
}else if(obj.material!=null){
result = obj.material.image
}
return result||'/static/img/student/noimage.jpg'
},
getTitle (row) {
let items = row.order_items,
result = ''
if(items.length>0){
items.forEach(i=>{
if(i.lesson){
result += i.lesson.lesson_name + ','
}
else if(i.material){
result += i.material.name + ','
}
})
result = result.substring(0,result.length-1)
}
return result
},
formatDate (time) {
return moment(time).format('YYYY-MM-DD hh:mm')
}
}
}



